import { nextTick, reactive, ref } from 'vue-demi';
import { isPage, replaceChildNode } from '@tmagic/utils';
export const useDsl = (app) => {
    const pageConfig = ref(app?.page?.data || {});
    app?.dataSourceManager?.on('update-data', (nodes, sourceId, changeEvent) => {
        nodes.forEach((node) => {
            if (isPage(node)) {
                pageConfig.value = node;
            }
            else {
                replaceChildNode(reactive(node), [pageConfig.value]);
            }
        });
        if (!app)
            return;
        nextTick(() => {
            app.emit('replaced-node', { nodes, sourceId, ...changeEvent });
        });
    });
    return {
        pageConfig,
    };
};
